import uniList from '@/components/uni-list/uni-list.vue';
import uniListItem from '@/components/uni-list-item/uni-list-item.vue';
import uniLoadMore from "@/components/uni-load-more/uni-load-more.vue"
import { post,tokenpost} from '@/api/user.js';
import { api } from '@/config/common.js';
import cuLoading from '@/components/custom/cu-loading.vue';
import WucTab from '@/components/wuc-tab/wuc-tab.vue';
import helangCheckbox from "@/components/helang-checkbox/helang-checkbox-jf.vue"
export default {
	components: {
		uniLoadMore,
		uniList,
		uniListItem,
		WucTab,
		"helang-checkbox":helangCheckbox
	},
	data() {
		return {
			loadmore:'more',
			pageIndex: 0,
			pageRows: 15,
			title: '积分兑换',
			pickerIndex:2,
			TabCur: 0,
			content_show_id:0,
			dataList: [],
			tabList: [{ name: '兑换' }, { name: '我的兑换' }],
			OrderStatusList:['待支付','已支付'],
			ordertype:3,
			PayTypeList:{
				'0': '',
				'1': '银行账号',
				'2': '微信',
				'3': '支付宝',
				'4': '现金'
			},
			productList: []
		};
	},
	onLoad(){
		// this.$refs.checkbox.set({
		// 	type:'radio',		// 类型：复选框
		// 	column:2,				// 分列：3
		// 	list:this.list	// 列表数据
		// });
		this.pickerIndex = 3;
		this.loadData();
		this.loadProduct();
	},
	onShow(){
	},
	mounted(){
	},
	methods: {
		handleRefreshPage() {
			console.log('refreshpage');
		},
		handleNavbarClickLeft() {
			uni.navigateBack({
				delta: 1
			})
		},
		tabChange(val) {
			console.log(val);
			switch (val){
				case 0:
					this.content_show_id = 0
					this.$refs.checkbox.set({
						type:'radio',		// 类型：复选框
						column:2,				// 分列：3
						list:this.list	// 列表数据
					});
					this.pickerIndex = 3;
					break;
				case 1:
					this.content_show_id =1
					break;
				default:
					this.content_show_id =0
				break;
			}
		},
		handleSubmit(){
			let data = this.$refs.checkbox.get();
			if(!data){
				this.$api.msg("请选择要兑换的产品!");
				return;
			}
			const sendData ={productid:data.productid};
			tokenpost(api.IntegralExchange,sendData).then(res => {
				if (res.status == 200 && res.data.returnCode == '0000') {
					this.$api.msg("兑换成功！");
					this.pageIndex = 0;
					this.loadData();
					this.TabCur=1;
					uni.$emit('changecompany',{'msg':'company变化了'});
				} else {
					this.$api.msg(res.data.returnMessage);
				}
			})
			.catch(error => {
				this.$api.msg('请求失败fail');
			});
			
		},
		loadProduct(){
			this.$refs.loading.open();
			const senddata = {
				pageIndex: 1,
				pageRows: -1,
				ordertype:this.ordertype,
				exchangeintegralbegin:1
			};
			tokenpost(api.GetProductList,senddata).then(res => {
				this.$refs.loading.close();
				if (res.status == 200 && res.data.returnCode == '0000') {
					this.productList = res.data.data.resultList;
					console.log(this.productList);
				} else {
					this.$api.msg(res.data.returnMessage);
				}
			})
			.catch(error => {
				this.loadmore = 'more',
				this.$refs.loading.close();
				this.$api.msg('请求失败fail');
			});
		},
		loadData(){
			this.loadmore = 'loading';
			this.$refs.loading.open();
			const senddata = {
				pageIndex: this.pageIndex+1,
				pageRows: this.pageRows
			};
			tokenpost(api.GetOrderList,senddata).then(res => {
				this.$refs.loading.close();
				if (res.status == 200 && res.data.returnCode == '0000') {
					if(res.data.data.resultList.length ===0){
						this.loadmore = "noMore"
						return;
					}else{
						this.dataList =this.dataList.concat(res.data.data.resultList);
						this.pageIndex = this.pageIndex+1 ;
						this.loadmore = "more"
					}

				} else {
					this.loadmore = 'more',
					this.$api.msg(res.data.returnMessage);
				}
			})
			.catch(error => {
				this.loadmore = 'more',
				this.$refs.loading.close();
				this.$api.msg('请求失败fail');
			});
		}
	}
};
